.App{ 
    width: 100vw;
    height: 100vh;
    display: flex;
    position:fixed;
    padding: 0;
    margin: 0;
    top: 0px;
    left:0px;
    flex-direction: column;
    justify-content: space-between;
    font-family: 'Helvetica', 'Arial', sans-serif;
    background-color: rgb(1, 103, 123);
    color:white;
}
.accessibility{
  border: 0; 
  clip: rect(0 0 0 0); 
  height: 1px; 
  margin: -1px; 
  overflow: hidden; 
  padding: 0; 
  position: absolute; 
  width: 1px; 
}
Button{ 
  border:none;
  background-color: rgb(1, 101, 121);
  color: white;
}
Button:hover{
  background-color: rgb(0, 217, 255);
}
input{
  border:none;
  padding: 5px;
}
h1{
  margin-left: 0;
  margin-right: 0;
}
h2{
  color: white;
  font-family: 'Helvetica', 'Arial', sans-serif;
  font-weight: bolder;
}

 /* width */
 ::-webkit-scrollbar {
    width: 10px;
  }
  
  /* Track */
  ::-webkit-scrollbar-track {
    background: rgb(1, 103, 123);
  }
  
  /* Handle */
  ::-webkit-scrollbar-thumb {
    background: white;
  }
  
  /* Handle on hover */
  ::-webkit-scrollbar-thumb:hover {
    background: rgb(0, 217, 255);
  } 
.sum {
    grid-area: header; 
    display:  flex;
    justify-content: space-between;
    align-items: center;
    background-color: black;
    width: 96vw;
    height: 7vh;
    font-family: 'Helvetica', 'Arial', sans-serif;
    padding: 2vh 2vw 0 2vw;
}
.logo{
    display: flex;
    align-items: center;
    color: white;
    font-size: 2em;
    font-weight: bolder;
    height: 2em
}
.logo-link {
    display:flex;
    cursor: pointer;
    height: 100%;
}
.logo-title{
    color: rgb(0, 217, 255);
}

@media only screen and (max-width:900px){
    .sum{
        display: grid;
        grid-template-areas:
            ". hilfe"
            ". .";
        height: 10vh;
        margin-bottom: 1vh;
    }
    .logo{
        font-size: 1.5em;
        height:1.5em;
    }
    .help{
        grid-area: hilfe;
        display: inline;
    }
}
@media only screen and (max-height:400px){
    .sum{
        display: flex;
        flex-direction:row;
        height: 14vh;
        margin-bottom: 2vh;
    }
    .help{
        display: inline;
    }
}
.Room{
    background-color:rgb(1, 103, 123);   
    width: 100vw;
    height: 100vh;
    object-fit: cover;
    position: fixed;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-template-rows: repeat(4, 1fr);
    grid-template-areas: 
        "header header header header" 
        "users  input  input  input" 
        "chat   video  video  video" 
        "footer footer footer footer";
}
.videoInput{
    grid-area: input;
    padding: 0 2vw 2vh 2vw;
}
.video {
    grid-area: video;
    display: flex;
    justify-content: center;
    align-self: center;
    height: 72vh;
    padding: 0 2vw 2vh 2vw;
}
.chat{
    grid-area: chat;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    background-color: rgb(26, 25, 25);
    border-right: rgb(0, 0, 0) 1px solid;
    list-style: none;
    margin: 0;
    height:72vh;
    padding: 0 2vw 2vh 2vw;
}
.users{
    grid-area: users;
    display: flex;
    flex-direction: column; 
    justify-content: center;
    align-items: center;
    background-color: rgb(26, 25, 25);
    border: 1px solid black;
    padding: 0 2vw 0 2vw;
}
h1 {
    font-family: 'Helvetica', 'Arial', sans-serif;
    color: white;
    font-style: bold;
    margin: 1em;
} 

@media only screen and (max-width:900px)
{
    .Room{
        grid-template-columns: 1fr;
        grid-template-rows: repeat(5, 1fr);
        grid-template-areas: 
            "header " 
            "input  " 
            "video  " 
            "chat   " 
            "footer ";
    }
    .video{
        height:25vh;
    }
    .chat{
        height:38vh;
    }
}

@media only screen and (max-height:400px){
    .Room{
        grid-template-columns: 1fr;
        grid-template-rows: repeat(3, 1fr);
        grid-template-areas: 
            "header " 
            "video  "
            "footer ";
    }
    .video{
        margin-top:2vh;
        height:70vh;
    }
}
.video-container {
    width: 72vw;
    height: 70vh;
    border: 1px solid black;
}
@media only screen and (max-width:900px){
    .video-container{
        width: 96vw;
        height: 25vh;
    }
}
@media only screen and (max-height:400px){
    .video-container{
        width: 96vw;
