.modal-help-content {
    z-index: 1;
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    background-color: black;
    padding: 2vh 2vw;
    height: 80vh;
    width: 80vw;
    color: white;
    overflow-y: hidden;
    font-family: 'Helvetica', 'Arial', sans-serif;
}
.help-element{
    z-index: 2;
    color: white;
    max-height: 5vh;
    padding: 0 0.5vw 0.5vh 0.5vw;
    margin: 1vh 0;
    overflow-y: hidden;
    background-color:rgb(1, 103, 123);
    transition: max-height 300ms ease-out;
}
.help-element:hover{
    max-height: 100vh;
    transition: max-height 400ms ease-in;
}
.help-element:focus{
    max-height: 100vh;
    transition: max-height  ;   
}
@media only screen and (max-width:900px){
    .modal-help-content{
        overflow-y: auto;
    }
    .help-element{
        height: auto;
        max-height: auto;
    }
    .help-element:hover{
        height: auto;
        max-height: auto;
    }
}
