    margin-right: 30px;
}
.modal-content {
    position: absolute;
    top: 40%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    background-color: black;
    padding: 1vh 2vw 1vh 2vw;
